import React from 'react'
import { Link, graphql } from 'gatsby'
import SEO from '../components/seo'
import Layout from '../components/Layout'

class BlogIndex extends React.Component {
  render() {
    const { data } = this.props
    const siteTitle = data.site.siteMetadata.title
    const posts = data.allMarkdownRemark.edges
    const { currentPage, numPages } = this.props.pageContext
    const prevPage = currentPage - 1 === 1 ? '/' : (currentPage - 1).toString()
    const nextPage = (currentPage + 1).toString()
    const isFirst = currentPage === 1
    const isLast = currentPage === numPages

    return (
      <Layout location={this.props.location} title={siteTitle}>
        <SEO
          title={siteTitle}
          keywords={[`blog`, `gatsby`, `javascript`, `react`]}
        />
        {posts.map(({ node }) => {
          const title = node.frontmatter.title || node.fields.slug
          return (
            <Link key={node.fields.slug} to={node.fields.slug} className='catalogue-item'> 
              <div>
                <time dateTime={node.frontmatter.date} className='catalogue-time'>{node.frontmatter.date}</time>
                <h1 className='catalogue-title'>{title}</h1>
                <div className='catalogue-line'></div>
                <p>{node.excerpt}</p>
              </div>
           </Link>
          )
        })}
        <div className="pagination">
          {prevPage && !isFirst && (
            <Link to={prevPage} rel="next" className="left arrow">
              &#8592;
            </Link>
          )}

          {nextPage && !isLast && (
            <Link to={nextPage} rel="prev" className="right arrow"> 
              &#8594;
            </Link>
          )}

          {
            currentPage && (
              <span>{currentPage}</span>
            )
          }
        </div>
        
      </Layout>
    )
  }
}

export default BlogIndex

export const pageQuery = graphql`
  query blogPageQuery($skip: Int!, $limit: Int!) {
    site {
      siteMetadata {
        title
      }
    }
    allMarkdownRemark(
      sort: { fields: [frontmatter___date], order: DESC }
      limit: $limit
      skip: $skip
    ) {
      edges {
        node {
          excerpt
          fields {
            slug
          }
          frontmatter {
            date(formatString: "DD MMMM, YYYY")
            title
          }
        }
      }
    }
  }
`
